
.VueStar{display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center;-ms-flex-pack:center;justify-content:center
}
.VueStar__icon{z-index:888;cursor:pointer;color:DarkGray
}
.red{color:#F05654
}
.VueStar__decoration{width:100px;height:100px;position:absolute;background:url("data:image/png;base64,") no-repeat;background-position:0 0;cursor:pointer;transition:background-position 1s steps(25);transition-duration:0s
}
.VueStar__decoration--active{transition-duration:1s;background-position:-2500px 0
}


